import { CanvasGenerator, CanvasReactor } from "@vis-three/convenient";

export default class BasicText extends CanvasReactor {
  constructor(
    config = {
      width: 512,
      height: 512,
      text: "默认文本",
      fontSize: "80px",
      color: "rgb(0, 0, 0)",
      backgroundColor: "rgb(255, 255, 255)",
      textAlign: "center",
      textBaseline: "middle",
    }
  ) {
    super(config);

    this.generator = new CanvasGenerator();
    this.canvas = this.generator.getDom();

    this.setSize().draw();
  }

  setSize() {
    this.generator.setSize(this.data.width, this.data.height);
    return this;
  }

  draw() {
    this.generator.clear().draw((ctx) => {
      const data = this.data;
      ctx.fillStyle = data.backgroundColor;
      ctx.fillRect(0, 0, data.width, data.height);

      ctx.font = `${data.fontSize} sans-serif`;
      ctx.fillStyle = data.color;
      ctx.textAlign = data.textAlign;
      ctx.textBaseline = data.textBaseline;
      ctx.fillText(data.text, data.width / 2, data.height / 2);
    });
    return this;
  }
}
